<template>
  <div class="sys-abnormal-handle-wrap">
    <el-row class="breadcrumb-style">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/sys-custom-deny-page-conf' }">系统配置</el-breadcrumb-item>
        <el-breadcrumb-item>拦截页面配置</el-breadcrumb-item>
      </el-breadcrumb>
    </el-row>
    <el-row class="container-style">
      <el-col :span="24" v-loading.fullscreen.lock="loadingPage">
        <el-form :model="defaultPageForm" :rules="rules" ref="defaultPageForm" label-width="150px">
          <el-form-item label="自定义拦截页面" key="1">
            <el-switch v-model="defaultPageForm.custom_deny_page" active-value="true" inactive-value="false">
            </el-switch>
          </el-form-item>
          <div v-if="defaultPageForm.custom_deny_page == 'true'">
            <el-form-item label="HTTP响应码" key="1" prop="waf_deny_code">
              <el-input v-model="defaultPageForm.waf_deny_code" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="响应内容" key="2">
              <el-radio-group v-model="waf_deny_html">
                <el-radio :label="0">无响应内容</el-radio>
                <el-radio :label="1">HTML响应内容</el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item v-show="waf_deny_html == 1">
              <el-input v-model="defaultPageForm.waf_deny_html" type="textarea" :autosize="{ minRows: 10 }" />
            </el-form-item>
          </div>
        </el-form>
        <el-row type="flex" class="margin-border" justify="space-between">
          <el-col :span="12"> </el-col>
          <el-col :span="12" class="text-align-right">
            <el-button type="primary" @click="onClickDefaultPageSubmit('defaultPageForm')" :loading="loading">保存
            </el-button>
          </el-col>
        </el-row>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import { mixin, JXAjax } from "../assets/scripts/common";
export default {
  mixins: [mixin],
  data() {
    return {
      loading:false,
      loadingPage: false,
      defaultPageForm: {},
      waf_deny_html: 0,
    };
  },
  computed: {
    rules() {
      return {
        waf_deny_code: [
          { required: true, message: '请输入', trigger: ["blur", "change"], },
        ],
      }
    }
  },

  mounted() {
    this.getData();
  },
  methods: {
    
    getData() {
      var t = this;
      JXAjax(
        "get",
        "/waf/waf_get_sys_custom_deny_page_conf",
        {},
        function (response) {
          t.loadingPage = false;
          t.defaultPageForm = response.data.message;
          if (t.defaultPageForm.waf_deny_html == "") {
            t.waf_deny_html = 0
          } else { 
            t.waf_deny_html = 1
          }
        },
        function () {
          t.loadingPage = false;
        }
      );
    },

    onClickDefaultPageSubmit(defaultPageForm) {
      var t = this;
      t.loading = true;
      var postUrl = "/waf/waf_edit_sys_custom_deny_page_conf";
      if (t.waf_deny_html ==  0) {
        t.defaultPageForm.waf_deny_html = ""
      } 
      this.$refs[defaultPageForm].validate((valid) => {
        if (valid) {
          t.loading = true;
          JXAjax(
            "post",
            postUrl,
            t.defaultPageForm,
            function (response) {
              t.loading = false;
              t.getData();
            },
            function () {
              t.loading = false;
            }
          );
        }
      });
    },
  },
};
</script>
<style>

.sys-abnormal-handle-wrap {
  max-width: 800px;
  min-width: 400px;
}
</style>
